.container{
    display: flex;
    }
    
    .dashboard{
        position: relative;
        width: 100%;
        z-index: 0;
    }
    
    .topBaseGradients{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 20rem;
        overflow: hidden;
        z-index: -1;
    }
    
    .topBaseGradients>:nth-child(2){
        left: 40%;
    }
    .topBaseGradients>:nth-child(3){
        right: 0;
    }
    
    .header{
        display: flex;
        padding: 2rem 4rem;
        align-items: center;
        justify-content: space-between;
    }
    
    .header > :nth-child(1){
        font-size: 1.2rem;
    }
    
    .searchBar{
        background-color: rgba(255, 255, 255, 0.496);
        width: 30%;
        max-width: 25rem;
        padding: 1rem;
        border-radius: 8px;
        height: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;
        border: 2px solid rgb(63, 63, 63);
    }
    
    .searchBar>input{
        background: transparent;
        color: white;
        outline: none;
        border: none;
        padding: 0;
        width: 100%;
    }
    
    .profile{
        display: flex;
        gap: 1rem;
        justify-content: center;
        align-items: center;
    }
    
    .profile > img{
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }
    
    .details{
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }
    
    .details > :nth-child(1){
        font-size: 1rem;
        font-weight: bold;
    }
    
    .content{
        margin: auto;
        display: flex;
        height: 100%;
        align-items: center;
        margin-top: -4rem;
    }
    
    .content > div{
        width: 100%;
        min-width: 75vh;
    }