.container {
    width: 100%;
    max-width: 130rem;
    padding: 0rem 4rem;
    display: flex;
    gap: 2rem;
    margin-top: 1rem;
  }
  
  .dashboard {
    width: 70%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .dashboardHead {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  
  .dashboardHead > .head {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .head:nth-child(1){
    font-size: 1.5rem;
    font-weight: 500;
  }

  .durationButton > select {
    border: none;
    background: var(--orange-gradient);
    padding: 10px;
    font-weight: 500;
    border-radius: 10px;
    font-size: 0.8rem;
  }

  .cards {
    display: flex;
    justify-content: space-between;
  }

  .card{
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  .cardHead{
    font-size: 1rem;
  }

  .cardHead>:nth-child(1)
{
  color: rgba(255, 255, 255, 0.835);
}

.cardHead>:nth-child(2){
    margin-left: 1.5rem;
    font-size: 0.8rem;
    color: var(--green);
  }

  .cardAmount>:nth-child(1)
{
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.776);
  padding: 4px;
}